.navigation {
  font-weight: bold;
}

a.navigation.clicked {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
}
.add-goal {
  font-weight: 900;
  z-index: 1;
}

.header {
  background-color: #ffffff;
}

.tooltip-inner {
  max-width: 350px;
  /* If max-width does not work, try using width instead */
  width: 350px;
}
</style>
